import React, { Component } from 'react';
import { Tabs, Row, Col } from 'antd';
import moment from 'moment';
import FillInSituation from './fillInSituation';
import PlanningReview from './planningReview';
import FillInReview from './fillInReview';
import styles from '../index.module.less';

const statusList = [
    {label:"",value:0,color:""},
    {label:"",value:2,color:"#FADB14"},
    {label:"",value:4,color:"#3F54A2"},
    {label:"",value:3,color:"#FA541C"},
    {label:"",value:5,color:"#FA8C16"},
    {label:"",value:6,color:"#52C41A"},
    {label:"",value:1,color:"#52C41A"},
];

export default class Detail extends Component {
    constructor(props){
        super(props);
        this.state = {
            detailInfo:{
                // planTaskId:"1",//id
                // planTaskName:"1",//
                // planAttachmentNum:1,//
                // planFillNum:5,//
                // taskStartTime:1234567,//
                // taskEndTime:1234567,//
                // creatorName:"",//
                // creatorDepartName:"",//
                // taskStatus:1,//
                // processId:1,//id
                // deliverType:0,
            },
            showExport:false
        }
    }
    componentWillMount(){
        let showExport = false;
        if(this.props.currentPlanInfo.taskStatus>=4){
            showExport = true;
        }
        this.setState({
            detailInfo:{...this.props.currentPlanInfo},
            showExport
        })
    }
    render() {
        const startTime = this.state.detailInfo.taskPlanStartTime?moment.unix(this.state.detailInfo.taskPlanStartTime/1000).format("YYYY/MM/DD HH:mm:ss"):"";
        const endTime = this.state.detailInfo.taskPlanEndTime?moment.unix(this.state.detailInfo.taskPlanEndTime/1000).format("YYYY/MM/DD HH:mm:ss"):"";
        return (
            <React.Fragment>
                <div className={styles.detailInfo}>
                    <Row style={{marginBottom:20}}>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}></span>
                            {this.state.detailInfo.planTaskName}
                        </Col>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}></span>
                            {`${startTime}-${endTime}`}
                        </Col>
                        <Col span={6}>
                            <span style={{display:"inline-block",width:100}}></span>
                            {statusList.find(item => item.value === this.state.detailInfo.taskStatus)?
                                statusList.find(item => item.value === this.state.detailInfo.taskStatus).label:""
                            }
                        </Col>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}></span>
                            {this.state.detailInfo.creatorName}
                        </Col>
                        <Col span={9}>
                            <span style={{display:"inline-block",width:100}}></span>
                            {this.state.detailInfo.creatorDepartName}
                        </Col>
                        <Col span={6}>
                            <span style={{display:"inline-block",width:100}}></span>
                            {this.state.detailInfo.deliverType===0?'':''}
                        </Col>
                    </Row>
                </div>
                <Tabs onChange={this.changeTab} type="card">
                    <Tabs.TabPane tab="" key="1">
                        <FillInSituation showExport={this.state.showExport} planTaskId={this.state.detailInfo.planTaskId}/>
                    </Tabs.TabPane>
                    <Tabs.TabPane tab="" key="2">
                        <PlanningReview planTaskId={this.state.detailInfo.planTaskId} processId={this.state.detailInfo.processId}/>
                    </Tabs.TabPane>
                    <Tabs.TabPane tab="" key="3">
                        <FillInReview planTaskId={this.state.detailInfo.planTaskId} processId={this.state.detailInfo.processId}/>
                    </Tabs.TabPane>
                </Tabs>
            </React.Fragment>
        )
    }
}
