import React, { Component } from 'react';
// import { Link } from 'react-router-dom';
import { Button, Table, Divider, Modal, message, Popconfirm, DatePicker} from 'antd';
import moment from 'moment';
import styles from './index.module.less';
import AttachmentTable from './attchmentTable';
import Detail from './detail';
import SearchBox from './searchBox';
import { postAction } from '@/axios';
import {hasButtonPermission} from '@/util'
const statusList = [
    {label:"",value:2,color:"#FADB14"},
    {label:"",value:4,color:"#3F54A2"},
    {label:"",value:3,color:"#FA541C"},
    {label:"",value:5,color:"#FA8C16"},
    {label:"",value:6,color:"#52C41A"},
    {label:"",value:1,color:"#9254DE"},
];

export default class index extends Component {
    constructor(props){
        super(props);
        this.state = {
            columns:[
                {
                    title:"",
                    dataIndex:"sortIndex",
                    width:"60px",
                },{
                    title:"",
                    dataIndex:"planTaskName",
                    render:text => (<span title={text}>{text}</span>)
                },{
                    title:"",
                    dataIndex:"planAttachmentNum",
                    width:"60px",
                    render:(text,record) => (
                        <span style={{textDecoration:"underLine",color:"blue",cursor:"pointer"}} onClick={()=>{this.showAttachmentModal(record)}}>
                            {text}
                        </span>
                    )
                },
                // {
                //     title:"",
                //     dataIndex:"fillCount",
                // },
                {
                    title:"",
                    dataIndex:"taskPlanStartTime",
                    render: (text) => {
                        let str = moment.unix(text/1000).format("YYYY-MM-DD");
                        return (<span title={str}>{str}</span>)
                    } 
                },{
                    title:"",
                    dataIndex:"taskPlanEndTime",
                    render: (text) => {
                        let str = moment.unix(text/1000).format("YYYY-MM-DD");
                        if(text){
                            return (<span title={str}>{str}</span>)
                        }else{
                            return "-"
                        }
                    } 
                },{
                    title:"",
                    dataIndex:"creatorName",
                    render:text => (<span title={text}>{text}</span>)
                },{
                    title:"",
                    dataIndex:"creatorDepartName",
                    render:text => (<span title={text}>{text}</span>)
                },{
                    title:"",
                    dataIndex:"taskStatus",
                    render:(text)=>{
                        const currentStatus = statusList.find(item => item.value===text);
                        if(currentStatus){
                            return (<span title={currentStatus.label}><i style={{display:"inline-block",width:8,height:8,borderRadius:"50%",marginRight:3,background:currentStatus.color}}/>{currentStatus.label}</span>)
                        }else{
                            return ""
                        }
                    }
                },{
                    title:"",
                    width:240,
                    render:(text,record) => {
                        if(record.taskStatus === 3 || record.taskStatus === 1 ){///
                            return (<React.Fragment>
                                {
                                    hasButtonPermission('fillPlan002') &&
                                    <span className="operation-link" onClick={()=>{this.showDetailModal(record)}}></span>

                                }
                                <Divider type="vertical"/>
                                {
                                    hasButtonPermission('fillPlan003') && 
                                    <span className="operation-link" onClick={()=>{this.editPlan(record)}}></span>

                                }
                                {
                                    record.taskStatus===1 && hasButtonPermission('fillPlan006')?
                                    <React.Fragment>
                                        <Divider type="vertical"/>
                                        <Popconfirm
                                            title=""
                                            onConfirm={()=>{this._deletePlan(record)}}
                                            okText=""
                                            cancelText=""
                                        >
                                            <span className="operation-link"></span>
                                        </Popconfirm>
                                    </React.Fragment>:null
                                }
                                {
                                    (record.taskStatus===5 || record.taskStatus===6)?null:(hasButtonPermission('fillPlan004')?
                                    <React.Fragment>
                                        <Divider type="vertical"/>
                                        <span className="operation-link" onClick={()=>{this._extendPlan(record)}}></span>
                                    </React.Fragment>:null
                                    )
                                }
                            </React.Fragment>)
                        }else{
                            return <React.Fragment>
                                {
                                    hasButtonPermission('fillPlan002') &&
                                    <span className="operation-link" onClick={()=>{this.showDetailModal(record)}}></span>

                                }
                                {
                                    (record.taskStatus===5 || record.taskStatus===6)?null:(hasButtonPermission('fillPlan004')?
                                    <React.Fragment>
                                        <Divider type="vertical"/>
                                        <span className="operation-link" onClick={()=>{this._extendPlan(record)}}></span>
                                        <Divider type="vertical"/>
                                    </React.Fragment>:null
                                    )
                                }
                                {
                                    record.taskStatus===2 && hasButtonPermission('fillPlan004') &&//
                                    <Popconfirm
                                        title=""
                                        onConfirm={()=>{this.recall(record)}}
                                        okText=""
                                        cancelText=""
                                    >
                                        <span className="operation-link"></span>
                                    </Popconfirm>
                                }
                            </React.Fragment>
                            
                        }
                    }
                }
            ],
            dataSource:[//
                // {
                //     planTaskId:"1",//id
                //     planTaskName:"1",//
                //     planAttachmentNum:1,//
                //     planFillNum:5,//
                //     taskStartTime:1234567,//
                //     taskEndTime:1234567,//
                //     creatorName:"",//
                //     creatorDepartName:"",//
                //     taskStatus:1,//
                // }
            ],
            currentPlanInfo:{},//
            showAttachmentModal:false,//
            showDetailModal:false,//
            pagination:{
                current:1,
                pageSize:10,
                total:0,
                showTotal:(total) => `${total}`
            },
            filterValue:{},//
            showExtendPlanModal:false,//
            taskPlanEndTime:null,//
        }
    }
    //
    showAttachmentModal = (record) => {
        this.setState({currentPlanInfo:record,showAttachmentModal:true})
    }
    //
    showDetailModal = (record) => {
        this.setState({currentPlanInfo:record,showDetailModal:true})
    }
    //
    recall = (record) => {
        postAction('/ybt/planTask/revokeSubmit',{planTaskId:record.planTaskId}).then(res=>{
            if(res.success){
                message.success('')
                this.getPlanListRequest()
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _extendPlan = (record) => {
        this.setState({
            currentPlanInfo:record,
            showExtendPlanModal:true
        })
    }
    ///
    _search = (params) => {;
        this.setState({
            filterValue:params
        },this.getPlanListRequest);
    }
    
    //---
    getPlanListRequest = (type) => {
        let pageNum = 1, pageSize = 10;
        if(type==="changePage"){
            pageNum = this.state.pagination.current;
            pageSize = this.state.pagination.pageSize
        }
        let params = {...this.state.filterValue};
        postAction(`/ybt/planTask/queryPlanList?pageNum=${pageNum}&pageSize=${pageSize}`,params).then(res => {
            if(res.success){
                this.setState({
                    pagination:{
                        current:res.obj.page,
                        pageSize:res.obj.pageSize,
                        total:res.obj.total,
                        showTotal:(total) => `${total}`
                    },
                    dataSource:res.obj.rows.map((item,index)=>({sortIndex:index+1,...item}))
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _changePagination = (pageInfo) => {
        this.setState({
            pagination:{
                current:pageInfo.current,
                pageSize:pageInfo.pageSize,
                total:pageInfo.total,
                showTotal:(total) => `${total}`
            }
        },()=>{this.getPlanListRequest("changePage")})
    }
    //
    addPlan = () => {
        //
        let info = {type:"add"}
        window.localStorage.setItem("fillPlanInfo",JSON.stringify(info))
        this.props.history.push("/fillPlan/step1")
    }
    //
    editPlan = (record) => {
        //
        // let record = {
        //         planTaskId:"1",//id
        //         planTaskName:"1",//
        //         planAttachmentNum:1,//
        //         planFillNum:5,//
        //         taskStartTime:1234567,//
        //         taskEndTime:1234567,//
        //         creatorName:"",//
        //         creatorDepartName:"",//
        //         taskStatus:1,//
        // }
        const planInfo = JSON.stringify({type:"edit",...record});
        window.localStorage.setItem("fillPlanInfo",planInfo)
        this.props.history.push("/fillPlan/step1")
    }
    //
    _deletePlan = (record) => {
        postAction("/ybt/planTask/updatePlanTask",{planTaskId:record.planTaskId,delFlag:1}).then(res=>{
            if(res.success){
                message.success("")
                this.getPlanListRequest();
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _updateTime = (value) => {
        // console.log(value)
        this.setState({
            taskPlanEndTime:value.valueOf()
        })
    }
    //
    _extendPlanRequest = () => {
        // console.log(this.state.taskPlanEndTime);
        if(!this.state.taskPlanEndTime){
            message.warning("");
            return false
        }
        postAction("/ybt/planTask/updatePlanTask",{planTaskId:this.state.currentPlanInfo.planTaskId,taskPlanEndTime:this.state.taskPlanEndTime}).then(res=>{
            if(res.success){
                message.success("");
                this._closeExtendPlanModal();
                this.getPlanListRequest();
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _closeExtendPlanModal = () => {
        this.setState({
            showExtendPlanModal:false,
            currentPlanInfo:{}
        })
    }
    render() {
        let oldTaskPlanEndTime = "";
        if(this.state.currentPlanInfo && JSON.stringify(this.state.currentPlanInfo!=="{}")){
            oldTaskPlanEndTime = moment.unix(this.state.currentPlanInfo.taskPlanEndTime/1000).format("YYYY/MM/DD HH:mm:ss");
        }
        return (
            <div className={styles.container}>
                <div className={styles.card}>
                    <div className={styles.cardTitle}>
                        
                        {/* <Link to="/homePage"></Link> */}
                    </div>
                    <SearchBox search = {this._search}/>
                    <div className={styles.filterBox}>
                        {
                            hasButtonPermission("fillPlan001") && <Button icon="plus" type="primary" onClick={this.addPlan}></Button>

                        }
                    </div>
                    <div className={styles.table}>
                        <Table
                            rowKey="sortIndex"
                            columns={this.state.columns}
                            dataSource={this.state.dataSource}
                            pagination={this.state.pagination}
                            onChange={this._changePagination}
                        />
                    </div>
                </div>
                <Modal
                    visible={this.state.showAttachmentModal}
                    footer={false}
                    onCancel={() => {this.setState({showAttachmentModal:false})}}
                    title={`${this.state.currentPlanInfo.planTaskName}`}
                    width={1136}
                    destroyOnClose={true}
                >
                    <AttachmentTable planTaskId={this.state.currentPlanInfo.planTaskId}/>
                </Modal>
                <Modal
                    visible={this.state.showDetailModal}
                    footer={false}
                    onCancel={() => {this.setState({showDetailModal:false})}}
                    title={`${this.state.currentPlanInfo.planTaskName}-`}
                    width={1236}
                    destroyOnClose={true}
                >
                    <Detail currentPlanInfo={this.state.currentPlanInfo}/>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.showExtendPlanModal}
                    onOk={this._extendPlanRequest}
                    onCancel={this._closeExtendPlanModal}
                    destroyOnClose={true}
                    width={400}
                >
                    <div style={{marginBottom:10}}>
                        <label style={{marginRight:20}}></label>
                        {oldTaskPlanEndTime}
                    </div>
                    <div>
                        <label style={{marginRight:20}}></label>
                        <DatePicker 
                            // showTime 
                            placeholder="" 
                            onChange={this._updateTime}
                            disabledDate={(startValue)=>{
                                let endValue = this.state.currentPlanInfo.taskPlanEndTime;
                                return startValue.valueOf() <  endValue;
                            }}
                        />
                    </div>
                </Modal>
            </div>
        )
    }
}
